<template>
	<view class="nlp-page">
		
		<view class="nlp-form">
			<view class="form-wrapper">
				<u-form :model="dataForm" ref="uForm">
					<u-form-item label="文章标题" prop="title" label-width="150rpx">
						<u-input v-model="dataForm.title" type="textarea" height="100rpx" placeholder="输入文章标题"  />
					</u-form-item>
					<u-form-item label="文章内容" prop="content" label-width="150rpx">
						<u-input v-model="dataForm.content" type="textarea" placeholder="输入文章内容" height="400rpx" />
					</u-form-item>
				</u-form>
				<u-button @click="queryHandle()"  class="login-btn">提交</u-button>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				result:{
					extractSummary:'',
					sentimentAnalysis:'',
					conceptClassify:'',
					entityRecognize:'',
					industryClassify:'',
					weightCalculation:''
				},
				dataForm: {
					title: "",
					content: ""
				},
				num: 60,
				rules: {
					title: [{
							required: true,
							message: '请输入文章标题',
							trigger: 'blur',
						},
					],
					content: [{
						required: true,
						message: '输入文章内容',
						trigger: 'blur'
					},{
						min: 10, 
						message: '文章内容不能少于10个字', 
						trigger: 'blur'
					}]
				}
			}
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		onLoad() {

		},

		methods: {
			queryHandle() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						uni.navigateTo({
							url: '/pages/access/access-znai/nlp-result?dataStr='+ JSON.stringify(this.dataForm)
						})
					} 
				});
			},
			
		}
	}
</script>
<style scoped>
.nlp-form{
	padding: 40rpx;
}
</style>

